import { Component, OnInit, ViewChild } from '@angular/core';
import { Model } from 'src/app/api/models/model';
import { RequestService } from 'src/app/api/request.service';
import { NavController, IonInfiniteScroll } from '@ionic/angular';
import { UtilService } from 'src/app/services/util.service';

@Component({
  selector: 'app-players',
  templateUrl: './players.page.html',
  styleUrls: ['./players.page.scss'],
})
export class PlayersPage implements OnInit {

  @ViewChild(IonInfiniteScroll) infiniteScroll: IonInfiniteScroll;


  playerModel : Model
  players = []
  firstLoading = true;
  oldPlayers = [];
  dataPaginate : any;

  constructor(
    public request : RequestService,
    public navCtrl : NavController,
    public util : UtilService

  ) {
    this.playerModel = new Model('Player',this.request);
  }

  ngOnInit() {
    this.load()
  }

  async load($event = null,paginate = false){

    let page = {};
    
    if(paginate && this.dataPaginate ){
      page = {
        page : this.dataPaginate.current_page + 1
      }
    }

    //await this.util.delay(4000)
    this.playerModel.api_function('pageHomePlayers',page).subscribe(
      response => {
        if(response['status'] == 'success'){
          this.dataPaginate = response['data'];
          if(!paginate){
            this.players = response['data']['data']
          }else{

            for(let player of response['data']['data']){
              this.players.push(player);
            }
            
          }
        }
        this.oldPlayers = [... this.players]
        console.log(response)
        this.firstLoading = false;
        if($event)
          $event.target.complete();
      },
      error => {
        this.firstLoading= false;
        console.error(error)
        if($event)
          $event.target.complete();
      }
    )
  }

  loadData(event) {
    if(this.dataPaginate &&  this.dataPaginate.last_page == this.dataPaginate.current_page){
      event.target.complete();
      event.target.disabled = true;
      this.infiniteScroll.disabled = !this.infiniteScroll.disabled;
      return;
    }

    this.load(event,true);
  }


  playerSelect(player){
    console.log('goTo',player)
    this.navCtrl.navigateForward(`player/profile/${player.id}`);
  
 }

 async searchPlayer(event){

  console.log('text search',event)
  let text = event.target.value;
  if(text.length <= 2){
    this.players = this.oldPlayers;
    return;
  }

  this.firstLoading = true;
 
  //await this.util.delay(4000);

  this.playerModel.api_function('searchPlayer',{query : text }).subscribe(
     response => {
       console.log(response);
       if(response['status'] == 'success'){
         this.players = response['data'];
         event.target.autofocus = true;
       }
       this.firstLoading = false;
     },
     error =>{
       console.error(error);
       this.firstLoading = false;
     }
   )
 }

 cancelSearch(event){
   this.players = this.oldPlayers;
 }


}
